* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Protest Strike", sans-serif;
  font-weight: 400;
}

input, button {
  outline: none;
  border: none;
}

body, #root, .app, .intro {
  width: 100vw;
  height: 100vh;
  overflow-x: hidden;
}

button, a {
  border-radius: 5px;
  background-color: #8F5EBF;
  color: #fff;
  cursor: pointer;
  transition: 0.5s;
}
button:active, a:active {
  transform: scale(0.95);
}
button:hover, a:hover {
  background-color: #996fc4;
}

.intro {
  background: linear-gradient(230deg, #F9F871, #FF9671, #FF6F91, #D65DB1, #845EC2);
  position: relative;
}
.intro .BG .BG1, .intro .BG .BG2 {
  position: absolute;
  height: 400px;
}
.intro .BG .BG1 {
  left: 2%;
}
.intro .BG .BG2 {
  bottom: 3%;
  right: 4%;
}
.intro .save_btn a {
  position: absolute;
  top: 2%;
  right: 3%;
  padding: 0.75rem 1rem;
}
.intro .intro_wrapper {
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  width: 100%;
  height: 100%;
  display: flex;
  place-content: center;
  flex-direction: column;
  text-align: center;
}
.intro .intro_wrapper .quote {
  font-size: 2rem;
  color: #fff;
  max-width: 65%;
  text-align: center;
  margin-inline: auto;
  text-shadow: 0px 4px rgba(217, 217, 217, 0.5803921569);
}
.intro .intro_wrapper .input_holder {
  width: 100%;
}
.intro .intro_wrapper .input_holder input, .intro .intro_wrapper .input_holder button {
  font-size: 1.05rem;
  color: #fff;
  height: 3rem;
  transition: 0.5s;
}
.intro .intro_wrapper .input_holder input {
  width: 30%;
  background: transparent;
  margin-top: 5rem;
  border: 2px solid;
  border-radius: 8px;
  padding: 0.45rem 0.55rem 0.45rem 0.875rem;
}
.intro .intro_wrapper .input_holder input::-moz-placeholder {
  color: #E4E4E4;
}
.intro .intro_wrapper .input_holder input::placeholder {
  color: #E4E4E4;
}
.intro .intro_wrapper .input_holder button {
  padding-inline: 2.5rem;
  margin-left: 0.65rem;
}

.tasks_Section {
  min-height: 100vh;
  padding-block: 8rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.tasks_Section .tasks_Container .task {
  position: relative;
  background: linear-gradient(90deg, #845EC2, #2C73D2, #0081CF, #0089BA, #008E9B);
  height: 45px;
  width: 750px;
  border-radius: 10px;
  display: flex;
  align-items: center;
  padding-left: 1.625rem;
  box-shadow: 4px 4px 6px rgba(122, 96, 195, 0.6509803922);
  margin-bottom: 1.25rem;
  -webkit-user-select: none;
     -moz-user-select: none;
          user-select: none;
  text-transform: capitalize;
}
.tasks_Section .tasks_Container .task:hover img {
  opacity: 1;
}
.tasks_Section .tasks_Container .task .task_title {
  font-size: 1.125rem;
  letter-spacing: 2px;
}
.tasks_Section .tasks_Container .task .number, .tasks_Section .tasks_Container .task .task_done, .tasks_Section .tasks_Container .task > img, .tasks_Section .tasks_Container .task .menu {
  position: absolute;
}
.tasks_Section .tasks_Container .task .number {
  left: -10%;
  color: #6F6F6F;
  font-size: 2rem;
}
.tasks_Section .tasks_Container .task .task_done {
  bottom: 15%;
  right: 5%;
  font-size: 0.75rem;
  color: #e7e7e7;
}
.tasks_Section .tasks_Container .task > img {
  width: 25px;
  right: 1%;
  opacity: 0;
  transition: 0.5s;
  cursor: pointer;
}
.tasks_Section .tasks_Container .task > img:active {
  transform: scale(0.9);
}
.tasks_Section .tasks_Container .task .menu {
  right: -17%;
  top: 1%;
  background: linear-gradient(90deg, #845EC2, #2C73D2, #0081CF, #0089BA, #008E9B);
  padding: 0.625rem;
  border-radius: 5px;
  font-size: 0.875rem;
  overflow: hidden;
  transition: 0.5s;
  opacity: 0;
}
.tasks_Section .tasks_Container .task .menu .check_task {
  color: #04cc3a;
}
.tasks_Section .tasks_Container .task .menu .delete_task {
  color: #ff3b3b;
}
.tasks_Section .tasks_Container .task .menu .uncheck_task {
  color: #dbdbdb;
}
.tasks_Section .tasks_Container .task .menu .change_color p {
  color: #fff;
}
.tasks_Section .tasks_Container .task .menu .change_color input {
  height: 20px;
  width: 20px;
  margin-left: 8px;
}
.tasks_Section .tasks_Container .task .menu .change_color button {
  padding: 0.375rem 0.5rem;
  margin-left: 0.375rem;
}
.tasks_Section .tasks_Container .task .menu img {
  height: 15px;
  margin-left: 8px;
}
.tasks_Section .tasks_Container .task .menu div {
  margin-bottom: 0.375rem;
  display: flex;
  align-items: center;
  cursor: pointer;
  transition: 0.5s;
}
.tasks_Section .tasks_Container .task .menu div:active {
  transform: scale(0.95);
}
.tasks_Section .tasks_Container .task .menu.hide {
  display: none;
}
.tasks_Section .tasks_Container .task .menu.opacity {
  opacity: 1;
}
.tasks_Section .tasks_Container .task.done {
  background: #626262;
}
.tasks_Section .tasks_Container .task.done .task_title {
  opacity: 0.6;
}
.tasks_Section .tasks_Container .noTasks {
  text-align: center;
}
.tasks_Section .tasks_Container .noTasks img {
  height: 400px;
  margin-bottom: 2rem;
}
.tasks_Section .tasks_Container .noTasks p {
  font-size: 2rem;
  letter-spacing: 2px;
  color: #4f5665;
}/*# sourceMappingURL=main.css.map */